
import Loading from 'vux/src/components/loading/index.vue'
import querystring from 'vux/src/tools/querystring/index.js'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'

import { mapState } from 'vuex'

import VConsole from "vconsole/dist/vconsole.min.js"
const PRODUCTION = 'production'

export default {
name: 'App',
directives: {
TransferDom
},
data () {
return {
show: true
}
},
components: {
Loading
},
computed: {
...mapState(['isLoading'])
},
created () {
if (process.env.NODE_ENV != PRODUCTION) {

}
const role_type = window.localStorage.getItem('role_type');
switch (role_type) {
case 'president':
document.title = '团卖物联-会长';
break;
case 'entrepreneur':
document.title = '团卖物联-创客';
break;
default:
break;
}

},
methods: {
_debug () {
let urlStr = location.href.split('?')[1]
let urlParams = querystring.parse(urlStr)
urlParams.debug && new VConsole()
}
}
}



