

import Vue from "vue"
import App from "./App"
import router from "./router"
import store from "./store"
import { i18n } from "./i18n"

import "@utils/rem"
import "@style/common"
import "@static/icon-font/iconfont.css"


import "@style/base-style"

import ToastPlugin from 'vux/src/plugins/toast/index.js'


Vue.use(ToastPlugin)

Vue.config.productionTip = false

const isAndroid = navigator.userAgent.indexOf('Android') > -1 || navigator.userAgent.indexOf('Adr') > -1; 
Vue.directive('resetInput', {

inserted: function (el) {
if(isAndroid){
return;
}

let input = document.getElementById('reset-input');

if (!input) {
input = document.createElement('INPUT');
input.type = 'text';
input.style.height = '0px'
input.style.width = '0px'
input.style.position = 'absolute'
input.id = 'reset-input';
document.body.prepend(input);
}

el.addEventListener('blur',function(){
input.focus();
input.blur();
});






}
});

/* eslint-disable no-new */
new Vue({
el: "#app",
router,
store,
i18n,
components: {
App
},
template: "<App/>"
})

router.beforeEach((to, from, next) => {
next()
})

router.afterEach((to, from, next) => {})



